import Vue from 'vue'
import App from './App.vue'
import axios from 'axios' // 引入 axios

Vue.config.productionTip = false

// 配置 Axios 基础 URL，指向你的后端 API 地址
axios.defaults.baseURL = '/api/'; // 修改为你的后端地址

// 将 axios 添加到 Vue 原型链，方便在组件中通过 this.$http 调用
Vue.prototype.$http = axios;

new Vue({
  render: h => h(App),
}).$mount('#app')